.main-navigation__list {
  display: flex;
  flex-direction: column;
  min-width: 280px;
  padding-top: 24px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    min-width: 440px;
    min-height: 36px;
    padding-top: 18px;
  }
}

.main-navigation__link {
  .title();
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;

  @media (min-width: @tablet-width) {
    .title();
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
